

html,
body {
  overscroll-behavior-y: contain;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
    sans-serif;
  position: fixed;
  overflow: hidden;
}

#root {
  background: #192028;
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39, auto;
}

#root > div {
  position: absolute;
  width: 100vw;
  height: 100vh;
  will-change: transform;
  display: flex;
  align-items: center;
  justify-content: center;
}


[class*="cardTopBottom-0"]{
  background-color: #FB5829 ;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

[class*="cardTopBottom-"]:not([class*="cardTopBottom-0"]) {
  background-color: #1a2128;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}



.topLink {
  width: 40px;
  margin-left: 10px;
  margin-top: 10px;
}

.topLink:hover {
  opacity: .5;
}

.topLinkLeft{
  width: 40px;
  position: absolute;
    top: 0px;

    right: 0px;
    margin-right: 10px;
    margin-top: 10px;
}

.topLinkLeft:hover {
  opacity: .5;
}

.topLinkLeftShifted{
  width: 40px;
  margin-left: 240px;
}

.topLinkLeftShifted:hover {
  opacity: .5;
}

.filler{
  width: 100px;
  margin-top: 10px;
  margin-left: 0px;
}

.bottomLink{
  width: 40px;
  position: absolute;
    bottom: 0px;
    margin-bottom: 10px;
    right: 0px;
    margin-right: 10px;
}
