.hoverFolder:hover{
  fill: grey;
  cursor: pointer;
}

.hoverFolder{
  font-family: 'Dosis', sans-serif;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

