#svg{
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}

.hidden{
  visibility: hidden
}



html,
body {
  overscroll-behavior-y: contain;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
    sans-serif;
  position: fixed;
  overflow: hidden;
}

#root {
  background: #192028;
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39, auto;
}

#root > div {
  position: absolute;
  width: 100vw;
  height: 100vh;
  will-change: transform;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}


[class*="cardTopBottom-0"]{
  background-color: #FB5829 ;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

[class*="cardTopBottom-"]:not([class*="cardTopBottom-0"]) {
  background-color: #1a2128;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 300px;
  height: 85vh;
  max-height: 570px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}



.topLink {
  width: 40px;
  margin-left: 10px;
  margin-top: 10px;
}

.topLink:hover {
  opacity: .5;
}

.topLinkLeft{
  width: 40px;
  position: absolute;
    top: 0px;

    right: 0px;
    margin-right: 10px;
    margin-top: 10px;
}

.topLinkLeft:hover {
  opacity: .5;
}

.topLinkLeftShifted{
  width: 40px;
  margin-left: 240px;
}

.topLinkLeftShifted:hover {
  opacity: .5;
}

.filler{
  width: 100px;
  margin-top: 10px;
  margin-left: 0px;
}

.bottomLink{
  width: 40px;
  position: absolute;
    bottom: 0px;
    margin-bottom: 10px;
    right: 0px;
    margin-right: 10px;
}

.card1,
.card2,
.card3,
.card4 {
  position: absolute;
  border-radius: 5px;
  background-size: cover;

  background-repeat: no-repeat;
  will-change: transform;
}

.container {
  margin-bottom: 200px;
  margin-left: 50px;
}

@media screen and (min-width: 717px) {
  .container {
    margin-bottom: 150px;
    margin-left: 150px;
  }
}
@media screen and (max-width: 717px) {
  .container{
    margin-bottom: 25vw;
    margin-left: 50px;
  }
}




.card1 {
  width: calc(100vw/2.65);
  height: calc(100vw/2.65);

  max-width: 250px;
  max-height: 250px;
  background-image: url(/static/media/computer.e34fe585.png);
}




.card4 {
  width: calc(100vw/8);
  height: calc(100vw/2.65);

  max-width: 100px;
  max-height: 250px;
  background-image: url(/static/media/jack.2415fff5.png);
}

